* {
    margin:0;
    padding:0;
    border:none;
}
body {
    background-color:#161617;
}
.slider {
    position:absolute;
    top:0%;
    height:100%; 
    width:100%;
    left:0;
    background-color: #1C1C1C;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index:2;
}


